html {
  box-sizing: border-box;
}
*,
*::before,
*::after {
  box-sizing: inherit;
}
body {
  margin: 0;
  padding: 0;
  background-color: #efebeb;
  color: #000000;
  font-size: 20px;
  font-family: "PT Serif", serif;
}
.primary-title {
  font-size: 100px;
  font-weight: 700;
  line-height: 90px;
}
.secondary-title {
  font-size: 70px;
  font-weight: 700;
  line-height: 68px;
}
.main-text {
  font-family: "Proxima Nova", sans-serif;
  font-size: 20px;
  font-weight: 400;
  line-height: 32px;
}
.main-button {
  text-decoration: none;
  cursor: pointer;
  background-color: #000000;
  font-size: 18px;
  padding: 13px 21px;
  padding-right: 13px;
  display: inline-flex;
  justify-content: space-between;
  align-items: center;
  min-width: 235px;
  border-radius: 18px;
  color: #fbbf28;
  border: none;
}
.main-button > span {
  display: inline-block;
  margin-left: 42px;
  width: 16px;
  height: 12px;
  background-image: url(../img/arrow-pointing-to-right.png);
  background-size: contain;
  background-repeat: no-repeat;
}
.quote-author {
  font-weight: 700;
  font-family: "PT Serif", serif;
}
/* pt-serif-regular - latin_cyrillic */
@font-face {
  font-family: 'PT Serif';
  font-style: normal;
  font-weight: 400;
  font-display: swap;
  src: local('PT Serif'), local('PTSerif-Regular'), url('../fonts/pt-serif-v11-latin_cyrillic-regular.woff2') format('woff2'), url('../fonts/pt-serif-v11-latin_cyrillic-regular.woff') format('woff');
}
/* pt-serif-700 - latin_cyrillic */
@font-face {
  font-family: 'PT Serif';
  font-style: normal;
  font-weight: 700;
  font-display: swap;
  src: local('PT Serif Bold'), local('PTSerif-Bold'), url('../fonts/pt-serif-v11-latin_cyrillic-700.woff2') format('woff2'), url('../fonts/pt-serif-v11-latin_cyrillic-700.woff') format('woff');
}
@font-face {
  font-family: 'Proxima nova';
  font-style: normal;
  font-weight: 400;
  font-display: swap;
  src: local('Proxima nova'), local('Proxima-nova'), url('../fonts/ProximaNova-Regular.woff2') format('woff2'), url('../fonts/ProximaNova-Regular.woff') format('woff');
}
.start-page {
  background-image: url(../img/text-cloud.png);
  min-height: 100vh;
  background-position: 64px bottom;
  background-repeat: no-repeat;
}
.start-page__header {
  padding-left: 64px;
  padding-top: 42px;
}
.logo {
  text-decoration: none;
  display: inline-block;
  width: 156px;
  height: 48px;
}
.logo__img {
  width: 100%;
  height: 100%;
  -o-object-fit: contain;
     object-fit: contain;
}
.start-page__desc-box {
  display: flex;
}
.start-page__desc-wrap {
  max-width: 488px;
  text-align: center;
  margin-top: 73px;
  margin-right: 108px;
  margin-left: auto;
}
.start-page__slogan {
  font-size: 24px;
  font-weight: 500;
  line-height: 68px;
  margin: 0;
  font-family: "Proxima Nova", sans-serif;
}
.start-page__title {
  margin: 0;
  margin-bottom: 36px;
}
.why-us {
  background-image: url(../img/why-us.png);
  min-height: 100vh;
  background-position: -645px bottom;
  background-repeat: no-repeat;
  position: relative;
}
.why-us__desc-box {
  max-width: 585px;
  padding-top: 240px;
  margin-left: 115px;
}
.why-us__title {
  margin: 0;
}
.why-us__cards-list {
  list-style: none;
  margin: 0;
  padding: 0;
  display: flex;
}
.why-us__card {
  width: 300px;
  height: 250px;
}
.why-us__card:first-child {
  width: 450px;
}
.why-us__card:not(:last-child) {
  margin-right: 30px;
}
.why-us__card-link {
  text-decoration: none;
  cursor: pointer;
  display: inline-block;
  width: 100%;
  height: 100%;
}
.why-us__card-img {
  -o-object-fit: contain;
     object-fit: contain;
}
.callback {
  background-image: url(../img/callback.png);
  min-height: 100vh;
  background-position: -375px -170px;
  background-repeat: no-repeat;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  position: relative;
}
.callback__fields-wrapper {
  max-width: 720px;
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  grid-row-gap: 20px;
  grid-column-gap: 20px;
}
.callback__form input {
  width: 348px;
  height: 50px;
  border-radius: 15px;
  background-color: #ffffff;
  color: #000000;
  border: none;
  padding-left: 30px;
  font-size: 18px;
  font-family: "Proxima Nova", sans-serif;
}
.callback__form textarea {
  resize: none;
  width: 348px;
  height: 190px;
  border-radius: 15px;
  border: none;
  padding-left: 30px;
  font-size: 18px;
  font-family: "Proxima Nova", sans-serif;
  color: #000000;
  padding-top: 19px;
}
::-webkit-input-placeholder {
  font-size: 18px;
  color: #000000;
  font-family: "Proxima Nova", sans-serif;
}
::-moz-placeholder {
  font-size: 18px;
  color: #000000;
  font-family: "Proxima Nova", sans-serif;
}
:-moz-placeholder {
  font-size: 18px;
  color: #000000;
  font-family: "Proxima Nova", sans-serif;
}
:-ms-input-placeholder {
  font-size: 18px;
  color: #000000;
  font-family: "Proxima Nova", sans-serif;
}
.form-label--textarea {
  grid-column-start: 2;
  grid-column-end: 3;
  grid-row-start: 1;
  grid-row-end: 4;
}
.callback__form-submit-button {
  margin-top: 50px;
}
.callback__form-wrapper {
  min-height: 100vh;
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  padding-left: 150px;
}
.callback__title {
  margin: 0;
  margin-bottom: 53px;
}
.callback__desc-box {
  max-width: 220px;
  margin-right: 70px;
  padding-top: 155px;
}
.callback__desc-box blockquote {
  margin: 0;
}
.callback__text.callback__quote-text {
  font-size: 32px;
  font-weight: 400;
  line-height: 42px;
}
.callback__footer {
  position: absolute;
  right: 0;
  bottom: 0;
  padding-right: 130px;
  padding-bottom: 57px;
}
